@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

body {
  margin: 0;
  background-color: #b3d6ff;
  font-family: 'Press Start 2P', cursive;
  color: #ff69b4;
  image-rendering: pixelated;
}

.container {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
  padding: 2rem;
}

.block {
  background: #fff0f6;
  border: 6px solid #ff69b4;
  border-radius: 16px;
  padding: 1rem;
  box-shadow: 6px 6px 0px #ffb3d1;
  text-align: center;
}

.block h2 {
  font-size: 1rem;
  background: #ffccff;
  display: inline-block;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  border: 3px solid #ff69b4;
  margin-bottom: 1rem;
}

.block a {
  color: #663399;
  text-decoration: none;
  display: inline-block;
  margin: 0.5rem 0;
  background: #ffe3f0;
  border: 2px dashed #ff69b4;
  padding: 0.4rem 0.8rem;
  border-radius: 10px;
}

.block a:hover {
  background: #ffccff;
  transform: scale(1.05);
  transition: 0.2s;
}

